<template>
	<div class="container">
		<my-header title="申请人信息"></my-header>
		<section>
			<div>
				<h3><i class="fa fa-user" aria-hidden="true"></i>申请人信息</h3>
				<div>
					<div>
						<span>与出险人关系：</span>
						<div>
							<cube-select v-model="relationship" :disabled=true :options="claimApply.appRelation"></cube-select>
						</div>
					</div>
					<div>
						<span>姓名：</span>
						<div>
							<cube-input @blur="RESETINPUT" v-model="userName" :disabled="isAdult"  placeholder="请输入姓名"></cube-input>
						</div>
					</div>
					<div>
						<div class="inputSty">
							<span>性别：</span>
							<div>
								<cube-select v-model="sex" :disabled="isAdult" :options="claimApply.appSex"></cube-select>
							</div>
						</div>
						<div class="inputSty">
							<span>证件类型：</span>
							<div>
								<cube-select v-model="idType" :disabled="isAdult" :options="claimApply.appCertiType"></cube-select>
							</div>
						</div>
					</div>
					<div>
						<span>出生日期：</span>
						<div>
							<date-picker :date='birth' placeholder="请选择出生日期" :disabled="isAdult" @selectDate='selectDate'></date-picker>
						</div>
					</div>
					<div>
						<span>证件号码：</span>
						<div>
							<cube-input @blur="RESETINPUT" v-model="cardId" type="number" :disabled="isAdult" placeholder="请输入证件号码"></cube-input>
						</div>
					</div>
					<div>
						<span>手机号码：</span>
						<div>
							<cube-input @blur="RESETINPUT" v-model="telephone" type="number" placeholder="请输入手机号码"></cube-input>
						</div>
					</div>
				</div>
				<h3><i class="fa fa-file-text" aria-hidden="true"></i>账户信息</h3>
				<div>
					<div>
						<span class="space"><span>户</span><span>名</span>：</span>
						<div>
							<cube-input @blur="RESETINPUT" v-model="account" placeholder="请输入户名"></cube-input>
						</div>
					</div>
					<div>
						<span class="space"><span>开</span><span>户</span><span>行</span>：</span>
						<div>
							<cube-select v-model="bank" placeholder="请选择开户行" :options="bankOption"></cube-select>
						</div>
					</div>
					<div>
						<span class="space"><span>银</span><span>行</span><span>帐</span><span>号</span>：</span>
						<div>
							<cube-input @blur="RESETINPUT" v-model="bankId" type="number" placeholder="请输入银行帐号"></cube-input>
						</div>
					</div>
					<div>
						<cube-checkbox v-model="isResident">仅为中国税收居民</cube-checkbox>
					</div>
					<div>
						<foot-button @next="toUpload"></foot-button>
					</div>
				</div>
			</div>
		</section>
	</div>
</template>
<script>
import MyHeader from "@/components/model/MyHeader";
import DatePicker from "@/components/model/DatePicker";
import {ISINPUTNULL,IDREG,TELREG} from "@/utils/publicUtils";
import FootButton from "@/components/pages/claimApply/components/FootButton";
import {APPLICANPEOPLEINFO,QUERYAPPLICANPEOPLEINFO} from "@/api";
import {mapState,mapMutations} from 'vuex';
import {bankData} from '@/assets/js/bank.js';
import {createAPI,Style,Checkbox,Input,Select} from 'cube-ui';
export default {
  name: 'ApplicantPeople',
  data(){
  	return {
  		relationship:"",
  		userName:"",
  		birth:"",
  		cardId:"",
  		telephone:"",
  		sex:"0",
  		idType:"0",
  		account:"",
  		bank:"",
  		bankId:"",
  		bankOption:bankData,
  		isResident:false
  	}
  },
  async created(){
	let datas=await QUERYAPPLICANPEOPLEINFO({claimNo:this.$route.query.claimNo});
	if(!datas.data.data){
		datas.data.data={}
	}
  	this.relationship=this.isAdult?"1":"2";
  	this.userName=this.isAdult?this.claimApply.claimtList.insuredName:"";
	this.sex=this.isAdult?this.claimApply.claimtList.insuredSex:"";
	this.idType=this.isAdult?this.claimApply.claimtList.insuredCertiType:"";
	this.birth=this.isAdult?this.claimApply.claimtList.insuredBirth:"";
	this.cardId=this.isAdult?this.claimApply.claimtList.insuredCertiCode:"";

	this.telephone=datas.data.data.appTel;
	this.isResident=!!datas.data.data.appCusidentity;
	this.account=datas.data.data.appAccountname;
	this.bank=datas.data.data.appBankCode;
	this.bankId=datas.data.data.appBankAccount;
  },
  computed:{
	...mapState(['claimApply','appCertiType','appRelation','appSex','claimtList']),
	isAdult(){
		let age=this.$Moment.duration(this.$Moment().diff(this.$route.query.insuredBirth)).years();
		if(age>18){
			 return true;
		}else{
			return false;
		}
	}
  },
  methods:{
  	...mapMutations(['RESETINPUT']),
  	async toUpload(){
  		ISINPUTNULL(this,this.userName,"申请人姓名")&&ISINPUTNULL(this,this.sex,"申请人性别")&&
  		ISINPUTNULL(this,this.idType,"证件类型")&&ISINPUTNULL(this,this.birth,"申请人出生日期")&&
  		ISINPUTNULL(this,this.cardId,"申请人证件号码")&&IDREG(this,this.cardId,"申请人证件号码")&&
  		ISINPUTNULL(this,this.telephone,"申请人手机号码")&&TELREG(this,this.telephone,"申请人手机号码")&&
  		ISINPUTNULL(this,this.account,"户名")&&ISINPUTNULL(this,this.bank,"开户行")&&
  		ISINPUTNULL(this,this.bankId,"银行帐号")&&ISINPUTNULL(this,this.isResident,"仅为中国税收居民")&&
  		await APPLICANPEOPLEINFO({
  			...this.$route.query,
  			"appRelation":this.relationship,
			"appName":this.userName,
			"appSex":this.sex,
			"appBirth":this.birth,
			"appCertiType":this.idType,
			"appCertiCode":this.cardId,
			"appTel":this.telephone,
			"appCusidentity":this.isResident?"1":"",
			"appAccountname":this.account,
			"appBankCode":this.bank,
			"appBankAccount":this.bankId
		}).then((res)=>{
			this.$router.push({path:this.$route.path+"/uploading",query:this.$route.query})
  		})
  	},
  	selectDate(val){
		this.birth=val;
	},
  },
  components:{
  	MyHeader,FootButton,DatePicker,
  	CubeInput:Input,
  	CubeSelect:Select,
  	CubeCheckbox:Checkbox
  }
}
</script>
<style lang="stylus" scoped>
	.container{
		height:100%;
		display:flex;
		flex-direction: column;
		&>section{
			background:#fff;
			flex:1;
			height:100%;
			overflow-y:auto;
			&>div{
				&>h3{
				padding:0 10px;
				background:rgb(242,242,242);
				height:40px;
				line-height:50px;
				font-size:15px;
				font-weight:700;
					.fa-user{
						color:#1296db;
						font-size:25px;
						margin-right:10px;
					}
					.fa-file-text{
						color:#e98f36;
						font-size:25px;
						margin-right:10px;
					}
				}
				&>div{
					padding:20px 10px;
					&>div{
						margin-bottom:12px;
						display:flex;
						align-items: center;
						.space{
							width:80px;
							display:flex;
						}
						>>>.cube-checkbox{
							padding-left:0;
						}
						&>div{
							flex:1
						}
						&>.inputSty{
							display:flex;
							flex:3;
							align-items:center;
							&>div{
								flex:1
							}
						}
						&>.inputSty:nth-child(2){
							flex:4;
							margin-left:20px;
							&>div{
								flex:1
							}
						}
					}
				}
			}
		}
	};
</style>


