<template>
	<div class="date">
		<cube-button @click="showDatePicker">
			<cube-input type="text" :placeholder="placeholder" readonly v-model="date"></cube-input>
		</cube-button>
	</div>
</template>

<script>
	import Vue from 'vue';
	import {Style,createAPI,DatePicker,Input,Button} from 'cube-ui';
	Vue.use(DatePicker)
	export default {
		props:{
			date:{
				type:String,
				default:''
			},
			placeholder:{
				type:String,
				default:"请选择时间"
			},
			disabled:{
				type:Boolean,
				default:false
		}},
		data() {
			return {
				dateTime:this.date
			};
		},
		methods:{
			showDatePicker(){
				if(this.disabled) return;
				if (!this.datePicker) {
				  this.datePicker = this.$createDatePicker({
						swipeTime:500,
						min: new Date(new Date().getFullYear()-70, 12, 1),
						max: new Date(new Date().getFullYear()+5, 11, 31),
						value: new Date(this.date?this.date:new Date()),
			            format: {
			              year: 'YYYY',
			              month: 'MM',
			              date: 'DD'
			            },
						onSelect: this.selectHandle,
				  })
				}
				this.datePicker.show()
			},
			selectHandle(date, selectedVal, selectedText) {
				this.dateTime=this.$Moment(date).format('YYYY-MM-DD');
				this.$emit('selectDate',this.dateTime);
			}
		},
		components:{
		  	CubeInput:Input,
		  	CubeButton:Button
		}
	}
</script>
<style scoped>
	.date{
		height: 100%;
	}
	>>>.cube-btn{
		width:100%;
		height: 100%;
		padding: 0;
  	}
	>>>.cube-btn input{
		width: 100%;
		height: 100%;
    font-size: 14px;
		outline: none;
	}
</style>



